package util

import js.core.jso
import react.EffectBuilder
import react.FC
import react.Props
import react.ReactElement
import react.create
import react.dom.client.createRoot as reactCreateRoot
import react.useEffect
import web.dom.Element

fun <T> jso(): T = jso()

fun createRoot(container: org.w3c.dom.Element) = reactCreateRoot(container.unsafeCast<Element>())

fun useEffectOnce(effect: EffectBuilder.() -> Unit) = useEffect(effect)
