                    }
                    +"hello@${props.name.lowercase()}.com"
                }

                div {
                    css {
                        marginBottom = 15.px
                    }
                    strong {
                        +"Phone: "
                    }
                    +"+1 (555) 123-4567"
                }

                div {
                    css {
                        marginBottom = 15.px
                    }
                    strong {
