                            }
                            +"Email"
                        }
                        input {
                            type = InputType.email
                            id = "email"
                            css {
                                width = 100.pct
                                padding = 10.px
                                borderRadius = 6.px
                                border = Border(1.px, LineStyle.solid, Color("#e2e8f0"))
                                fontSize = 16.px
                            }
