                                marginBottom = 5.px
                                color = Color("#64748b")
                            }
                            +"Message"
                        }
                        textarea {
                            id = "message"
                            css {
                                width = 100.pct
                                padding = 10.px
                                borderRadius = 6.px
                                border = Border(1.px, LineStyle.solid, Color("#e2e8f0"))
