external interface FooterProps : Props {
    var name: String
}

val Footer = FC<FooterProps> { props ->
    footer {
        css {
            backgroundColor = Color("white")
            textAlign = TextAlign.center
            padding = Padding(30.px, 0.px)
            marginTop = 50.px
        }

        p {
            css {
                color = Color("#64748b")
            }
            +" ${js("new Date().getFullYear()")} ${props.name}. All Rights Reserved."
        }
    }
}

val App = FC<Props> {
    // State for tracking active section
    val (activeSection, setActiveSection) = useState("home")
    val profileName = "John Doe"

    // Effect to handle scroll and update active section
    useEffect {
        val handleScroll: (Event) -> Unit = {
            val sections = listOf("home", "about", "skills", "works", "contact")

            // Find the section that is currently in view
            val currentSection = sections.find { sectionId ->
                val element = document.getElementById(sectionId) ?: return@find false
                val rect = element.getBoundingClientRect()
                rect.top <= 100 && rect.bottom >= 100
            } ?: "home"

            setActiveSection(currentSection)
        }

        window.addEventListener("scroll", handleScroll)

        // Clean up event listener
        cleanup {
            window.removeEventListener("scroll", handleScroll)
        }
    }

    // Apply global styles
    globalStyles()

    // Container
    div {
        css {
            maxWidth = 1200.px
            margin = Margin(0.px, Auto.auto)
            padding = Padding(0.px, 20.px)
        }

        // Header
        +Header.create {
            this.activeSection = activeSection
            this.profileName = profileName
        }

        // Main content
        main {
            // Hero section
            +HeroSection.create {
                this.name = profileName
            }

            // About section
            +AboutSection.create {}

            // Skills section
            +SkillsSection.create {}

            // Works section
            +WorksSection.create {}

            // Contact section
            +ContactSection.create {
                this.name = profileName
            }
        }

        // Footer
        +Footer.create {
            this.name = profileName
        }
    }
}
