package component

import csstype.*
import emotion.react.css
import react.FC
import react.Props
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.h2
import react.dom.html.ReactHTML.h3
import react.dom.html.ReactHTML.section

val SkillsSection = FC<Props> {
    section {
        id = "skills"
        css {
            padding = Padding(100.px, 0.px)
        }

        h2 {
            css {
                fontSize = 36.px
                marginBottom = 30.px
                color = Color("#1e293b")
            }
            +"My Skills"
        }

        div {
            css {
                display = Display.grid
                gridTemplateColumns = "repeat(auto-fit, minmax(250px, 1fr))".unsafeCast<GridTemplateTracks>()
                gap = 20.px
            }

            // Skill items
            listOf(
                "UI/UX Design" to 90,
                "HTML & CSS" to 95,
                "JavaScript" to 85,
                "React" to 80,
                "Figma" to 85,
                "Adobe XD" to 80
            ).forEach { (skill, percentage) ->
                div {
                    css {
                        backgroundColor = Color("white")
                        padding = 20.px
                        borderRadius = 10.px
                        boxShadow = BoxShadow(0.px, 4.px, 6.px, rgba(0, 0, 0, 0.05))
                    }

                    h3 {
                        css {
                            margin = Margin(0.px, 0.px, 15.px, 0.px)
                            fontSize = 18.px
                            color = Color("#1e293b")
                        }
                        +skill
                    }

                    div {
                        css {
                            height = 8.px
                            backgroundColor = Color("#e2e8f0")
                            borderRadius = 4.px
                            overflow = Overflow.hidden
                        }

                        div {
                            css {
                                height = 100.pct
                                width = "$percentage%".unsafeCast<Width>()
                                backgroundColor = Color("#4169e1")
                            }
                        }
                    }

                    div {
                        css {
                            textAlign = TextAlign.right
                            marginTop = 5.px
                            fontSize = 14.px
                            color = Color("#64748b")
                        }
                        +"$percentage%"
                    }
                }
            }
        }
    }
}
